/*媒体查询*/
@media (max-width: 768px) {
.nav-box {
    background: #1BBC9B;
    margin-top: 0!important;
}
}
.container-fluid {
  padding: 0!important;
}
.icon-bar {
  background: #333333;
}
.navbar-toggle {
  margin-top: 20px;
}
.top-box {
  /*display: none;*/
  background: #fff;
  position: fixed;
  top: 0;
  width: 100%;
  margin: 0 auto;
  height: 80px;
  /*overflow: hidden;*/
  z-index: 1000;
}
.top-box .logo-box {
  float: left;
}
.top-box .logo-box img {
  margin-top: 11px;
}
.top-box .nav-box {
  float: right;
  margin-top: 25px;
  overflow: hidden;
}
.top-box .nav-box .nav-list {
  float: left;
  text-align: center;
  line-height: 30px;
  transition: all 0.9s ease;
}
.top-box .nav-box .nav-list.cur {
  background: #1BBC9B;
  color: #fff;
}
.top-box .nav-box .nav-list.cur a {
  color: #fff;
}



