
@charset "utf-8";
@media (max-width: 768px) {
.msg-box input[type="text"] {
    width: 80%!important;
    margin: 15px auto!important;
}
.msg-box textarea {
    display: block!important;
    margin: 4px auto!important;
}
}
.contact-box {
  overflow: hidden;
  width: 100%;
  min-height: 100%;
  padding-bottom: 80px;
  background: url("../assets/contactbg.jpg") no-repeat;
  background-size: cover;
}
.contact-title {
  margin-top: 100px;
  color: #333;
  text-align: center;
  height: 30px;
  line-height: 30px;
  font-size: 2em;
  margin-bottom: 20px;
}
.contact-con {
  overflow: hidden;
}
/*.contact-con-left{
  width: 20%;
  .fl()
}
.contact-con-right{
  width: 80%;
  .fl()
}*/
.contact-con-right {
  margin: 0 auto;
}
.contact-con-left .contact-detail {
  text-align: center;
  margin: 0 auto;
  margin-top: 20px;
}
.contact-con-left .contact-detail h4 {
  color: #282828;
  font-size: 20px;
  font-weight: 700;
  margin: 0;
}
.contact-con-left .contact-detail p {
  color: #474747;
  font-size: 15px;
  font-weight: 500;
  margin-top: 10px;
  line-height: 1.6em;
  font-family: 'Open Sans', sans-serif;
}
.msg-box input[type="text"] {
  display: block;
  width: 45%;
  font-size: 13px;
  margin: 15px 0;
  color: #474747;
  background: #f3f3f3;
  padding: 10px 15px;
  outline: none;
  border: none;
  border-radius: 2.3em;
  -webkit-border-radius: 2.3em;
  -moz-border-radius: 2.3em;
  -o-border-radius: 2.3em;
  -ms-border-radius: 2.3em;
  font-family: 'Open Sans', sans-serif;
}
.msg-box textarea {
  display: block;
  width: 80%;
  height: 100px;
  margin: 4px 0;
  font-size: 14px;
  color: #474747;
  resize: none;
  background: #f3f3f3;
  padding: 15px;
  outline: none;
  border: none;
  border-radius: 2.3em;
  -webkit-border-radius: 2.3em;
  -moz-border-radius: 2.3em;
  -o-border-radius: 2.3em;
  -ms-border-radius: 2.3em;
  font-family: 'Open Sans', sans-serif;
}
.msg-box button {
  display: block;
  float: right;
  margin-top: 30px;
  margin-right: 18%;
  color: #414141;
  padding: 7px 25px;
  font-size: 16px;
  cursor: pointer;
  font-weight: 700;
  border-radius: 2.3em;
  -webkit-border-radius: 2.3em;
  -moz-border-radius: 2.3em;
  -o-border-radius: 2.3em;
  -ms-border-radius: 2.3em;
  border: 2px solid #1BBC9B;
  background: #fff;
}



