
@media (max-width: 768px) {
.work-detail .work-list {
    height: 150px!important;
}
.mask-box p.w-name {
    margin: 25px auto!important;
    font-size: 16px!important;
}
.mask-box p.w-link,
  .mask-box .w-link a,
  .mask-box p.w-link img,
  .mask-box .w-link a img {
    width: 40px!important;
    height: 40px!important;
}
}
.col-sm-3,
.col-md-3 {
  padding: 0!important;
}
.work-box {
  overflow: hidden;
  width: 100%;
  min-height: 100%;
  background: url("../assets/workbg.jpg") no-repeat;
  background-size: cover;
}
.work-title {
  margin-top: 100px;
  color: #333;
  text-align: center;
  height: 30px;
  line-height: 30px;
  font-size: 2em;
  margin-bottom: 20px;
}
.work-detail {
  width: 90%;
  margin: 0 auto;
  overflow: hidden;
  padding-bottom: 80px;
}
.work-detail .work-list {
  position: relative;
  /*width: 25%;*/
  height: 230px;
  overflow: hidden;
  float: left;
}
.work-detail .work-list img {
  transition: all 2s ease;
  position: absolute;
  width: 100%;
  z-index: 2;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.mask-box {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  overflow: hidden;
}
.mask-box p.w-name {
  margin: 50px auto;
  font-size: 20px;
  color: #fff;
  text-align: center;
}
.mask-box p.w-link,
.mask-box .w-link a {
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #F1F1F1;
  margin: 0 auto;
  cursor: pointer;
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0;
}
.mask-box p.w-link img,
.mask-box .w-link a img {
  text-align: center;
  width: 50px;
  height: 50px;
  vertical-align: top;
}



