
@media (max-width: 768px) {
.skill-detail .about-title {
    margin-top: 0!important;
}
}
.skill-detail {
  /*float: left;
  width: 50%;*/
}
.skill-box {
  /*width: 380px;*/
  overflow: hidden;
  margin: 0 auto;
}
.skill-list {
  height: 34px;
  /*border-radius: 5px;*/
  color: #ffffff;
  background: #1BBC9B;
  text-align: center;
  /*width: 80px;*/
  float: left;
  line-height: 34px;
  cursor: pointer;
  /*margin-right: 20px;*/
  margin-top: 10px;
}
.skill-list.cur {
  color: #12745f;
}
.skill-list:nth-last-child(1) {
  margin-right: 0;
}
.sk-con-list {
  overflow: hidden;
  padding-bottom: 80px;
}
.sk-con {
  /*width:@w;*/
  height: 50px;
  color: #f1f1f1;
  font-size: 19px;
  text-align: center;
  line-height: 50px;
  float: left;
  /*margin-left:20px;*/
  margin-top: 25px;
}



